import { Injectable } from '@angular/core';
import { HttpClient,  HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
// import { Payment } from '../game/game.component';



export interface Payment {
  userID: string;
  amount: number;
}


@Injectable({
  providedIn: 'root'
})
export class BackendService {

  

  // private readonly URL = 'http://localhost:3000/'; //dev mode

  private readonly URL = 'http://localhost:3000/'; //prod mode

  constructor(private http: HttpClient) {

   

   }

    headers =  {
    headers: new  HttpHeaders({ 
      'Content-Type': 'application/json'})
  };

  getTimer(): Observable<any> {


    // Using the POST method
   
    return this.http.get(this.URL+ 'timer/get', this.headers);
  }
  

  getRecords(): Observable<any>{

    return this.http.get(this.URL + 'records/get', this.headers);
  }


  getPayments(userID): Observable<any>{

    return this.http.post(this.URL + 'payments/getbyID/' + userID, this.headers);

  }

  updatePayments( userID,  amount): Observable<Payment>{

    const body = {userID: userID, amount: amount }
    return this.http.post<Payment>(this.URL + 'payments/update/', body, this.headers);

  }
}
